import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Button,
  Card,
  CardContent,
  Grid,
  Chip,
  IconButton,
  Tooltip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  CircularProgress,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow
} from '@mui/material';
import {
  Add,
  Edit,
  Delete,
  LocalParking,
  Refresh,
  Visibility
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';
import axios from 'axios';

interface Espacio {
  id_espacio: number;
  numero_espacio: string;
  tipo_vehiculo: 'auto' | 'camioneta' | 'camion' | 'moto';
  tarifa_hora: number;
  estado: 'libre' | 'ocupado' | 'reservado' | 'mantenimiento';
  fecha_creacion: string;
}

interface EspacioForm {
  numero_espacio: string;
  tipo_vehiculo: 'auto' | 'camioneta' | 'camion' | 'moto';
  tarifa_hora: number;
  estado: 'libre' | 'ocupado' | 'reservado' | 'mantenimiento';
}

const Espacios: React.FC = () => {
  const { user } = useAuth();
  const [espacios, setEspacios] = useState<Espacio[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [editingEspacio, setEditingEspacio] = useState<Espacio | null>(null);
  const [formData, setFormData] = useState<EspacioForm>({
    numero_espacio: '',
    tipo_vehiculo: 'auto',
    tarifa_hora: 0,
    estado: 'libre'
  });

  const fetchEspacios = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await axios.get('/api/espacios');
      setEspacios(response.data.data);
      
    } catch (error: any) {
      console.error('Error al cargar espacios:', error);
      setError('Error al cargar los espacios');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchEspacios();
  }, []);

  const handleOpenDialog = (espacio?: Espacio) => {
    if (espacio) {
      setEditingEspacio(espacio);
      setFormData({
        numero_espacio: espacio.numero_espacio,
        tipo_vehiculo: espacio.tipo_vehiculo,
        tarifa_hora: espacio.tarifa_hora,
        estado: espacio.estado
      });
    } else {
      setEditingEspacio(null);
      setFormData({
        numero_espacio: '',
        tipo_vehiculo: 'auto',
        tarifa_hora: 0,
        estado: 'libre'
      });
    }
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setEditingEspacio(null);
    setFormData({
      numero_espacio: '',
      tipo_vehiculo: 'auto',
      tarifa_hora: 0,
      estado: 'libre'
    });
  };

  const handleSubmit = async () => {
    try {
      if (editingEspacio) {
        await axios.put(`/api/espacios/${editingEspacio.id_espacio}`, formData);
      } else {
        await axios.post('/api/espacios', formData);
      }
      
      fetchEspacios();
      handleCloseDialog();
    } catch (error: any) {
      console.error('Error al guardar espacio:', error);
      setError(error.response?.data?.error || 'Error al guardar el espacio');
    }
  };

  const handleDelete = async (id: number) => {
    if (window.confirm('Ests seguro de que quieres eliminar este espacio?')) {
      try {
        await axios.delete(`/api/espacios/${id}`);
        fetchEspacios();
      } catch (error: any) {
        console.error('Error al eliminar espacio:', error);
        setError(error.response?.data?.error || 'Error al eliminar el espacio');
      }
    }
  };

  const getStatusColor = (estado: string) => {
    switch (estado) {
      case 'libre':
        return 'success';
      case 'ocupado':
        return 'error';
      case 'reservado':
        return 'warning';
      case 'mantenimiento':
        return 'info';
      default:
        return 'default';
    }
  };

  const getTipoVehiculoLabel = (tipo: string) => {
    switch (tipo) {
      case 'auto':
        return 'Auto';
      case 'camioneta':
        return 'Camioneta';
      case 'camion':
        return 'Camin';
      case 'moto':
        return 'Moto';
      default:
        return tipo;
    }
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
        <CircularProgress size={60} />
      </Box>
    );
  }

  return (
    <Box>
      {/* Header */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Box>
          <Typography variant="h4" component="h1" gutterBottom sx={{ color: '#1976d2', fontWeight: 600 }}>
             Gestin de Espacios
          </Typography>
          <Typography variant="body1" color="text.secondary">
            Administra los espacios de parqueamiento del sistema
          </Typography>
        </Box>
        
        <Box sx={{ display: 'flex', gap: 2 }}>
          <Button
            variant="outlined"
            startIcon={<Refresh />}
            onClick={fetchEspacios}
          >
            Actualizar
          </Button>
          {user?.rol === 'admin' && (
            <Button
              variant="contained"
              startIcon={<Add />}
              onClick={() => handleOpenDialog()}
            >
              Nuevo Espacio
            </Button>
          )}
        </Box>
      </Box>

      {/* Estadsticas rpidas */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <Typography variant="h4" color="primary">
                {espacios.length}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Total de Espacios
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <Typography variant="h4" color="success.main">
                {espacios.filter(e => e.estado === 'libre').length}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Espacios Libres
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <Typography variant="h4" color="error.main">
                {espacios.filter(e => e.estado === 'ocupado').length}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Espacios Ocupados
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <Typography variant="h4" color="warning.main">
                {espacios.filter(e => e.estado === 'mantenimiento').length}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                En Mantenimiento
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Mensaje de error */}
      {error && (
        <Alert severity="error" sx={{ mb: 3 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {/* Tabla de espacios */}
      <Card>
        <CardContent>
          <Typography variant="h6" gutterBottom sx={{ mb: 3 }}>
            Lista de Espacios
          </Typography>

          {espacios.length === 0 ? (
            <Box sx={{ textAlign: 'center', py: 4, color: 'text.secondary' }}>
              <LocalParking sx={{ fontSize: 60, mb: 2, opacity: 0.3 }} />
              <Typography variant="h6" gutterBottom>
                No hay espacios registrados
              </Typography>
              <Typography variant="body2">
                Comienza agregando el primer espacio de parqueamiento.
              </Typography>
            </Box>
          ) : (
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Nmero</TableCell>
                    <TableCell>Tipo de Vehculo</TableCell>
                    <TableCell>Tarifa por Hora</TableCell>
                    <TableCell>Estado</TableCell>
                    <TableCell>Fecha Creacin</TableCell>
                    <TableCell align="center">Acciones</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {espacios.map((espacio) => (
                    <TableRow key={espacio.id_espacio} hover>
                      <TableCell>
                        <Typography variant="body2" fontWeight={600}>
                          {espacio.numero_espacio}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Chip
                          label={getTipoVehiculoLabel(espacio.tipo_vehiculo)}
                          color="secondary"
                          size="small"
                        />
                      </TableCell>
                      <TableCell>
                        <Typography variant="body2">
                          S/ {typeof espacio.tarifa_hora === 'number' ? espacio.tarifa_hora.toFixed(2) : '0.00'}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Chip
                          label={espacio.estado}
                          color={getStatusColor(espacio.estado) as any}
                          size="small"
                        />
                      </TableCell>
                      <TableCell>
                        <Typography variant="body2" color="text.secondary">
                          {new Date(espacio.fecha_creacion).toLocaleDateString()}
                        </Typography>
                      </TableCell>
                      <TableCell align="center">
                        <Box sx={{ display: 'flex', gap: 1, justifyContent: 'center' }}>
                          <Tooltip title="Ver detalles">
                            <IconButton size="small" color="primary">
                              <Visibility />
                            </IconButton>
                          </Tooltip>
                          
                          {user?.rol === 'admin' && (
                            <>
                              <Tooltip title="Editar">
                                <IconButton
                                  size="small"
                                  color="primary"
                                  onClick={() => handleOpenDialog(espacio)}
                                >
                                  <Edit />
                                </IconButton>
                              </Tooltip>
                              
                              <Tooltip title="Eliminar">
                                <IconButton
                                  size="small"
                                  color="error"
                                  onClick={() => handleDelete(espacio.id_espacio)}
                                >
                                  <Delete />
                                </IconButton>
                              </Tooltip>
                            </>
                          )}
                        </Box>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          )}
        </CardContent>
      </Card>

      {/* Dialog para crear/editar espacio */}
      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
        <DialogTitle>
          {editingEspacio ? 'Editar Espacio' : 'Nuevo Espacio'}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            <TextField
              fullWidth
              label="Nmero de Espacio"
              value={formData.numero_espacio}
              onChange={(e) => setFormData({ ...formData, numero_espacio: e.target.value })}
              margin="normal"
              required
            />
            
            <FormControl fullWidth margin="normal" required>
              <InputLabel>Tipo de Vehculo</InputLabel>
              <Select
                value={formData.tipo_vehiculo}
                label="Tipo de Vehculo"
                onChange={(e) => setFormData({ ...formData, tipo_vehiculo: e.target.value as any })}
              >
                <MenuItem value="auto">Auto</MenuItem>
                <MenuItem value="camioneta">Camioneta</MenuItem>
                <MenuItem value="camion">Camin</MenuItem>
                <MenuItem value="moto">Moto</MenuItem>
              </Select>
            </FormControl>
            
            <TextField
              fullWidth
              label="Tarifa por Hora (S/)"
              type="number"
              value={formData.tarifa_hora}
              onChange={(e) => setFormData({ ...formData, tarifa_hora: parseFloat(e.target.value) || 0 })}
              margin="normal"
              required
              inputProps={{ min: 0, step: 0.01 }}
            />
            
            <FormControl fullWidth margin="normal" required>
              <InputLabel>Estado</InputLabel>
              <Select
                value={formData.estado}
                label="Estado"
                onChange={(e) => setFormData({ ...formData, estado: e.target.value as any })}
              >
                <MenuItem value="libre">Libre</MenuItem>
                <MenuItem value="ocupado">Ocupado</MenuItem>
                <MenuItem value="reservado">Reservado</MenuItem>
                <MenuItem value="mantenimiento">Mantenimiento</MenuItem>
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Cancelar</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editingEspacio ? 'Actualizar' : 'Crear'}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Espacios;
